/**
 * Constantes pour les events
 */
var EVENTS={

    /**
     * un lment devient visible ou masqu
     */
    VISIBILITY_CHANGE:"VISIBILITY_CHANGE",
    /**
     * un lment vient devient visible
     */
    VISIBLE:"VISIBLE",
    /**
     * un element vient d'tre masqu
     */
    HIDDEN:"HIDDEN",
    /**
     * Rediemtionnement d'un lement (la fentre navigateur par exemple ;)
     */
    RESIZE:"RESIZE",
    /**
     * Ev^nement de scroll
     */
    SCROLL:"SCROLL",
    /**
     * Mouvement de souris
     */
    MOUSE_MOVE:"MOUSE_MOVE",
    /**
     * Changement d'orientation
     */
    ORIENTATION_CHANGE:"ORIENTATION_CHANGE",
    /**
     * Entre ou sortie du full screen
     */
    FULL_SCREEN_CHANGE:"FULL_SCREEN_CHANGE",
    /**
     * Entre en mode full screen
     */
    FULL_SCREEN_ENTER:"FULL_SCREEN_ENTER",
    /**
     * Sortie du full screen
     */
    FULL_SCREEN_CANCEL:"FULL_SCREEN_CANCEL",

    CLOSE:"CLOSE",
    OPEN:"OPEN",

    SAVED:"SAVED",
    /**
     * Pour dsigner un vnement de slection
     */
    SELECT:"SELECT",
    /**
     * History tape 1 Quand l'url change
     */
    HISTORY_CHANGE_URL:"HISTORY_CHANGE_URL",
    /**
     * History tape 2 Quand l'url a chang et qu'on a commenc  charger la page
     */
    HISTORY_CHANGE_URL_LOADING:"HISTORY_CHANGE_URL_LOADING",
    /**
     * History tape 3 Quand l'url a chang et que la page est charge (mais pas injecte)
     */
    HISTORY_CHANGE_URL_LOADED:"HISTORY_CHANGE_URL_LOADED",
    /**
     * History tape 4 Quand l'url a chang, que la page est charge ET injecte dans le DOM
     */
    HISTORY_CHANGE_URL_LOADED_INJECTED:"HISTORY_CHANGE_URL_LOADED_INJECTED",
    /**
     * l'upload de fichier est termin
     */
    UPLOAD_COMPLETE:"UPLOAD_COMPLETE",

    /**
     * Emis quand un nouvel objet xdebug est instanci, le paramtre est l'objet xdebug
     */
    XDEBUG_DETECTED:"XDEBUG_DETECTED",

    /**
     * Evnement server qui dit si l'utilisateur est loggu en wysiwyg ou non (voir dans vars)
     * @type {string}
     */
    SSE_USER_IS_WYSIWYG:"SSE_USER_IS_WYSIWYG",
    /**
     * Evnement server qui dit que l'utilisateur vient de se logger
     * @type {string}
     */
    SSE_USER_LOGIN:"SSE_USER_LOGIN",
    /**
     * Evnement server qui dit que l'utilisateur vient de se d-logger
     * @type {string}
     */
    SSE_USER_LOGOUT:"SSE_USER_LOGOUT",
    /**
     * Evnement server qui envoie une log
     * @type {string}
     */
    SSE_DEBUG_LOG:"debug.log",
    /**
     * Quand le serveur nous dit que le nombre de records a chang
     */
    SSE_DB_COUNT_CHANGE:"SSE_DB_COUNT_CHANGE",
    /**
     * Quand le serveur nous dit qu'un record a t effac
     */
    SSE_DB_TRASH:"SSE_DB_TRASH",
    /**
     * Quand le serveur nous dit qu'un record a t modifi
     */
    SSE_DB_CHANGE:"SSE_DB_CHANGE",
    /**
     * Evnement server qui dit qu'il y a eu une modification sur les utilisateurs
     */
    SSE_USER_CHANGE:"SSE_USER_CHANGE",
    /**
     * Evnement server qui dit d'afficher une info
     * @type {string}
     */
    SSE_INFO:"info",

    /**
     * ready ou dom change
     */
    DOM_CHANGE_OR_READY:"POV_EVENT_READY POV_EVENT_DOM_CHANGE",
    /**
     * le dom a chang
     */
    DOM_CHANGE:"POV_EVENT_DOM_CHANGE"
};

window.EVENTS=EVENTS;


